/* ---- 404----- */
.errorrr{
  transition: all 0.6s;
  height: 100%;
  font-family: 'Lato', sans-serif;
  color: #888;
  margin: 0;
}

#mainError{
  display: table;
  width: 100%;
  height: 100vh;
  text-align: center;
}

.fof{
  display: table-cell;
  vertical-align: middle;
}

.fof h1{
  font-size: 50px;
  display: inline-block;
  padding-right: 12px;
  -webkit-animation: type .5s alternate infinite;
          animation: type .5s alternate infinite;
}

@-webkit-keyframes type{
  from{box-shadow: inset -3px 0px 0px #888;}
  to{box-shadow: inset -3px 0px 0px transparent;}
}

@keyframes type{
  from{box-shadow: inset -3px 0px 0px #888;}
  to{box-shadow: inset -3px 0px 0px transparent;}
}

/* ---Box Response--- */

.boxResp h1, h2 {
  font-family: 'Crete Round', serif;
}


.boxResp{
  background:#343a40;
  width: 80%;
  max-width: 960px;
  min-height: 3.750em; 
  margin: 2em auto;
  padding: 1.250em;
  border-radius: 0.313em;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.5);
  line-height: 1.5em;
  color: white;
}

.ribbon{
  position:relative;
  padding: 0 0.5em;
  font-size:2.000em;
  margin: 0 0 0 -0.625em;
  line-height: 1.875em;
  color: #000F2C;
  border-radius: 0 0.156em 0.156em 0;
  background: #F999A5;
  box-shadow: -1px 2px 3px rgba(0,0,0,0.5);
}

.ribbon:before, .ribbon:after{
  position:absolute;
  content: '';
  display: block;
}

.ribbon:before{
  width: 0.469em;
  height: 100%;
  padding: 0 0 0.438em;
  top:0;
  left: -0.469em;
  background:inherit;
  border-radius: 0.313em 0 0 0.313em;
}

.ribbon:after{
  width: 0.313em;
  height: 0.313em;
  background: rgba(0,0,0,0.35);
  bottom: -0.313em;
  left: -0.313em;
  border-radius: 0.313em 0 0 0.313em;
  box-shadow: inset -1px 2px 2px rgba(0,0,0,0.3);
}

/* ---Search box ---- */ 
.form-search{
  position: relative;
  /* top: 50%;
  left: 50%;
  transform: translate(-50%,-50%); */
  transition: all 1s;
  width: 50px;
  height: 50px;
  /* background: white; */
  box-sizing: border-box;
  border-radius: 25px;
  border: 4px solid white;
  padding: 5px;
}

.form-search input{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;;
  height: 42.5px;
  line-height: 30px;
  outline: 0;
  border: 0;
  display: none;
  font-size: 1em;
  border-radius: 20px;
  padding: 0 20px;
}

.fa{
  box-sizing: border-box;
  padding: 10px;
  width: 42.5px;
  height: 42.5px;
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 50%;
  color: #07051a;
  text-align: center;
  font-size: 1.2em;
  transition: all 1s;
}

.form-search:hover{
  width: 200px;
  cursor: pointer;
}

.form-search:hover input{
  display: block;
}

.form-search:hover .fa{
  background: #07051a;
  color: white;
}


@-webkit-keyframes tipsy {
    0% {
      -webkit-transform: translateX(-50%) translateY(-50%) rotate(0deg);
              transform: translateX(-50%) translateY(-50%) rotate(0deg);
    }
    100% {
      -webkit-transform: translateX(-50%) translateY(-50%) rotate(360deg);
              transform: translateX(-50%) translateY(-50%) rotate(360deg);
    }
  }
  
  @keyframes tipsy {
    0% {
      -webkit-transform: translateX(-50%) translateY(-50%) rotate(0deg);
              transform: translateX(-50%) translateY(-50%) rotate(0deg);
    }
    100% {
      -webkit-transform: translateX(-50%) translateY(-50%) rotate(360deg);
              transform: translateX(-50%) translateY(-50%) rotate(360deg);
    }
  }
  
  .judulnya {
    font-family: helvetica, arial, sans-serif;
    background-color: #2e2e31;
    text-align: center;
  }
  
  .putar {
    color: #fffbf1;
    /* text-shadow: 0 20px 25px #5a4b58, 0 40px 60px #5a4b58; */
    font-size: 40px;
    font-weight: bold;
    text-decoration: none;
    letter-spacing: -3px;
    margin: 0;
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
  
  .putar:before,
  .putar:after {
    content: '';
    padding: .9em .4em;
    position: absolute;
    left: 50%;
    width: 120%;
    top: 50%;
    display: block;
    border: 5px solid #F999A5;
    -webkit-transform: translateX(-50%) translateY(-50%) rotate(0deg);
            transform: translateX(-50%) translateY(-50%) rotate(0deg);
    -webkit-animation: 10s infinite alternate ease-in-out tipsy;
            animation: 10s infinite alternate ease-in-out tipsy;
  }
  
  .putar:before {
    border-color: #F999A5 #F999A5 rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
    z-index: -1;
  }
  
  .putar:after {
    border-color: rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) #F999A5 #F999A5;
    box-shadow: 7px 7px 7px #5a4b58;
  }
.box{
    text-align: center;
    color: #F999A5;
    font-size: 14px;
    margin-left: auto;
    margin-right: auto;
}


.box input[type = "text"], .box input[type = "password"], .box input[type = "email"]{
    border: 0;
    background: none;
    display: black;
    text-align: center;
    border: 2px solid #555f66;
    /* padding: 14px 20px; */
    width: 87%;
    outline: none;
    color: white;
    border-radius: 30px;
    transition: 0.2s;
}

.box input[type = "text"]:focus, .box input[type = "password"]:focus, .box input[type = "email"]:focus{
    width: 95%;
    border-color: #F999A5;
}

.box button[type ="submit"]{
    border: 0;
    background: none;
    display: black;
    text-align: center;
    border: 2px solid #F999A5;
    outline: none;
    color: white;
    border-radius: 50px;
    transition: 0.2s;
    cursor: pointer;
}

.box button[type="submit"]:hover{
    color: #000F2C;
    background: #F999A5;
}
.gantiCard, .firstinfo {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}

.gantiCard{
  font-family: 'Open Sans', sans-serif;
  width: 100%;
  min-height: 100%;
  font-size: 16px;
  overflow: hidden;
}

.gantiCard, .gantiCard:before, .gantiCard:after {
  box-sizing: border-box;
}

.content {
  position: relative;
  -webkit-animation: animatop 0.9s cubic-bezier(0.425, 1.14, 0.47, 1.125) forwards;
          animation: animatop 0.9s cubic-bezier(0.425, 1.14, 0.47, 1.125) forwards;
}

 .gantiCard .cards {
  width: 500px;
  min-height: 100px;
  padding: 20px;
  border-radius: 3px;
  background-color: white;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
  position: relative;
  overflow: hidden;
}
 .gantiCard .card:after {
  content: '';
  display: block;
  width: 190px;
  height: 300px;
  background: cadetblue;
  position: absolute;
  -webkit-animation: rotatemagic 0.75s cubic-bezier(0.425, 1.04, 0.47, 1.105) 1s both;
          animation: rotatemagic 0.75s cubic-bezier(0.425, 1.04, 0.47, 1.105) 1s both;
}

.firstinfo {
